import React, { Component } from 'react'

export default class productImg extends Component {
    state = { prdImgLoaded: false, noImg: false }
    render() {
        const { imgUrl } = this.props;
        const { prdImgLoaded, noImg } = this.state;
        return (
            <div className={`ugc-product-images ${noImg ? `tb_popup_image` : ''}`} style={{ display: noImg ? 'none' : 'block' }}>
                <img src={imgUrl} style={{ height: '0%', display: 'none' }} onLoad={() => this.setState({ prdImgLoaded: true })} onError={() => this.setState({ noImg: true })} alt='' />
                {!prdImgLoaded ? <div className="sk_loader_product_thumb"><div></div></div>
                    : <div className="ugc_pro_img_holder " style={{ backgroundImage: `url(${imgUrl})` }}><div></div> </div>}

            </div>
        )
    }
}