import React, { PureComponent } from "react";
import reactImageSize from 'react-image-size';
import HotSpot from '../../../../Themes/Elements/HotSpot'

export default class Image extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      height: '',
      padding: '',
      error: '',
    };
  }

  componentWillMount() {
    const { ImageUrl } = this.props
    let w,h,x;
    reactImageSize(ImageUrl)
      .then(({ width, height }) => {
      x = width > height ? true : false;
      w = x ? 100 : (width*100)/height;
      h= x ? (height*100)/width :100;
      this.setState({
        height: h,
        width: w
      })})
      .catch((errorMessage) =>
        this.setState({
          height: 100,
          width: 100,
          error: errorMessage
        })
      )

  }




  render() {
    const { ImageClass, ImageUrl, item,ugc_products, product } = this.props
    const { width, height } = this.state
    const Image = {
      backgroundImage: !height ? `url(${ImageUrl})`: `url(${ImageUrl})`, height: width > height ? height+'%':'100%', width: width > height ? '100%':width+'%', padding:'unset'
    };
    return (
      <div className={ImageClass} style={Image}>
        {(item.type == 2 || item.type == 4) && (item.hotspot == 1 && item.ugc_personalizaion.UgcSetting.status == 1) ? <HotSpot product={product} item={item} ugc_products={ugc_products} UgcSetting={item.ugc_personalizaion.UgcSetting} /> : <div></div>}
      </div>
    );
  }
}

