import React, { PureComponent } from "react";
import PopUpMedia from "./PopUpMedia";
import '@splidejs/splide/css';
import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide';

export default class PopUpMediaSlider extends PureComponent {
 state={
    activeSlider:0,
}
  
changeActive=(next)=>{
 this.setState({activeSlider:next})
}

  render() {
    const { item, instaImg, ugc_products, isVideoError, wall } = this.props
    const {activeSlider} = this.state;
    const settings = { 
      rewind: true,
      speed: 1000,
      gap: 0,
      autoplay:false,
      padding: 0,
      pagination: true,
      arrows: true,
      focus: 'center',
      Slide:"123456",
      classes: {
        pagination: 'splide__pagination tb_modal_slider_dots',
        page      : 'splide__pagination__page tb_modal_slider_dot',
      },
    };
    return <div className="tb_post_modal_media_wrapper___">
         <div className="tb_post_modal_slide_holder_">
      <Splide hasTrack={false} className="tb_post_modal_media_slider" options={settings} ref={this.myRef} onMove={ (prev, next)=>{this.changeActive(next)}} >
        <SplideTrack>
          {item.imageList.map((imageItem, index) => <SplideSlide style={{margin:0, padding:0}} key={`psId${index}`}>
             <div className="tb_post_modal_slide_list">
               <PopUpMedia wall={wall} item={imageItem} instaImg={instaImg} ugc_products={ugc_products} isVideoError={isVideoError} custmClass={"ts_popup_slider"} ImageUrl={imageItem.postFile} videoUrl={imageItem.mediaFile} active={index==activeSlider ? true :false}/>
              </div>
            </SplideSlide>
          )}
        </SplideTrack>
        <div className="splide__arrows splide__arrows--ltr tb_post_modal_arrow_wrapper_">
          <div className="splide__arrow splide__arrow--prev tb_post_modal_arrow tb_post_modal_arrow_left__ tb__icon tb-chevron-left"><div></div></div>
          <div className="splide__arrow splide__arrow--next  tb_post_modal_arrow tb_post_modal_arrow_right__ tb__icon  tb-chevron-right"><div></div></div>
        </div>
      </Splide>
      
    </div>
    </div>
  }
}