import React, { Component } from 'react';
import moment from "moment";
import { TWITTER_LOGO_FN } from '../../../../utils';

const fontColorIfWhite = (white, color, defaultColor, cardColor) => {
    if (cardColor === white) return white == color ? defaultColor : color;
    else if (cardColor === defaultColor) return defaultColor == color ? white : color;
    return white == color ? defaultColor : color;
}


class PopUpAuthor extends Component {
   



    render() {
        const { item, personalization, wall, networkData } = this.props;
        const authorColor = fontColorIfWhite("#ffffff", wall.ThemeRule.authorColor, "#000000", wall.ThemeRule.cardColor);
        const isInstaUser = item.author.isInstaUser; const postAuthor = personalization.postAuthor;
        const nerworkColor = wall.ThemeRule.iconType == 505 ? networkData.color : wall.ThemeRule.iconColor=='#ffffff'? 'rgba(63, 66, 72, 0.3)':wall.ThemeRule.iconColor;
        const tb_authorColor = { color: authorColor, textDecoration:'none' };
        return (<>
          

            <div className="tb_post_author">
                {!(postAuthor == 0 && personalization.commerceTheme != 4) ?
                 !isInstaUser ?
                    <div className="tb_author_details">
                        <div className="tb_sc_author_profile"> 
                            <img src={item.author.picture} onError={(e)=>{e.target.onerror = null;  e.target.src=item.author.errorPic}} alt="" /> 
                        </div> 
                       <div className="tb_author_detail_outer">
                        <div className="tb_author_usrname" style={tb_authorColor}>
                            <a href={item.link} target="_blank" className="m_post_title" rel="noreferrer" style={tb_authorColor}>{item.author.name}</a></div>
                            <div className="tb_author_info">
                                <div className="tb_post_time" style={tb_authorColor}>{moment( new Date(item.createdAt * 1000) ).fromNow()}</div>
                            </div>
                       </div>
                    </div>
                    : '': ''}
                <div className="tb_social_ico" data-network={networkData.name}>
                    <div className={`tb__icon tb-${networkData.icon.replace('fa-','')}`} style={{ color: nerworkColor }}><div></div></div>
                </div>
            </div>
            </>
        );
    }
}

export default PopUpAuthor;