import React,{ PureComponent, Suspense, lazy } from 'react';
import { connect } from 'react-redux';
import { closePopUP } from '../../../../../../../../../actions/themeActions/themeActions';
import {removeTransFromCss} from '../../../utils'

import PostPopUp from './PostPopUp'
const SubmitPic = lazy(() => import('../UserComponents/SubmitPic'));

class ModalPopUp extends PureComponent {
    state = { isEnable: 0, eventData: {}, shareText: "SHARE", viewOnText: "View on", currentIndex: null, completeData: {}, personalization: {}, url: null, platform: null, imgData: [], type: "" }

    closeCommercePopUP = event => {
        removeTransFromCss('remove')
        this.setState({ isEnable: 0, currentIndex: null, type: "" });
        this.props.closePopUP();

        var taggBoxRoot = document.getElementsByTagName('html');
        if (taggBoxRoot && taggBoxRoot[0]) {
          taggBoxRoot[0].style.overflow = "";
        }
    };

    getCommercePopUPEvent = (data) => {
        if (data !== undefined && data !== "" && data.card) {
            if (data.type === "post") {
                this.setState({ url: "", isEnable: 1, eventData: data, currentIndex: data.index, completeData: data.idArray, viewOnText: data.viewOnText, shareText: data.shareText, windowParent: data, imgData: data.imgData, personalization: data?.personalization, type: data.type }, () => this.CommercePopUPSlideWithArrowKey());
            }
            else if (data.type === "submit-pic") { this.setState({ isEnable: 1, type: data.type, windowParent: data, }) }
        }

    };



    /* POP SLIDER CHANGE INDEX WITH SLIDE */
    featuredPopSliderChangeIndex = (currentIndex) => event => {
        const { eventData } = this.state;
        if (currentIndex === -1) this.setState({ currentIndex: eventData.idArray.length - 1 });
        else this.setState({ currentIndex: eventData.idArray.length === currentIndex ? 0 : currentIndex });
    };

    SliderWithKey = (currentIndex) => {
        const { eventData } = this.state;
        if (currentIndex === -1) this.setState({ currentIndex: eventData.idArray.length - 1 });
        else this.setState({ currentIndex: eventData.idArray.length === currentIndex ? 0 : currentIndex });
        
    };

    componentDidMount() {
        const { data } = this.props;
        this.getCommercePopUPEvent(data);

        var taggBoxRoot = document.getElementsByTagName('html');
        if (taggBoxRoot && taggBoxRoot[0]) {
          taggBoxRoot[0].style.overflow = "hidden";
        }

    }

    /* POP SLIDER WITH ARROW KEYS EVENTS */
    CommercePopUPSlideWithArrowKey = e => {
        const { personalization } = this.state;

        if (personalization && personalization.popupSlideShow === 1) {
            e = e || window.event;
            let self = this;
            if (e && e.keyCode && e.keyCode !== undefined) {
                if (e.keyCode === 37) self.SliderWithKey(self.state.currentIndex - 1)
                else if (e.keyCode === 39) self.SliderWithKey(self.state.currentIndex + 1)
                else if (e.keyCode === 27) this.closeCommercePopUP();
            }
        }
    }



    render() {
        const { viewOnText, isEnable, currentIndex, personalization, completeData, eventData, url, platform, imgData, type } = this.state;
        let data = null;
        if (type === "post") {
            if (currentIndex === -1) data = completeData[eventData.idArray.length - 1];
            else data = completeData[currentIndex];
        }
        const { wall, webFilters } = this.props;

        return (isEnable === 1 && wall && type === "submit-pic") ?

        <Suspense fallback={null}><SubmitPic wall={wall} wallId={wall.Wall.id} onClosePopUp={this.closeCommercePopUP} /></Suspense>
            : isEnable === 1 && type === "post" ?
                <PostPopUp  ctaSetting={this.props.ctaSetting} webFilters={webFilters} wall={wall} item={data} onClosePopUp={this.closeCommercePopUP} currentIndex={currentIndex} personalization={personalization} viewOnText={viewOnText} slideWithArrowKeys={this.CommercePopUPSlideWithArrowKey} featuredPopSliderChangeIndex={this.featuredPopSliderChangeIndex} hostUrl={url} platform={platform} imgData={imgData} />
                : null
    }
}








const mapStateToProps = state => { return { wall: state.appData.wall, webFilters: state.appData.webFilters, ctaSetting: state.appData.ctaSetting, } }
const mapDispatchToProps = dispatch => { return { closePopUP: (data) => dispatch(closePopUP(data)), } }
export default connect(mapStateToProps, mapDispatchToProps)(ModalPopUp);

