import { Helmet } from "react-helmet";
import React, { Component } from 'react';
import { TAGGSHOP_FONT_URL} from "../../../constants"

export default class PopUpStyles extends Component {
  render() {
      const {networkData, ctaSetting} = this.props;
   

    return  ctaSetting &&  Object.keys(ctaSetting).length > 0 ? <Helmet>
        {ctaSetting?.title_link_font ? <link href={`${TAGGSHOP_FONT_URL}/${ctaSetting?.title_link_font}.css`} rel="stylesheet" /> : null}
        {ctaSetting?.price_link_font ? <link href={`${TAGGSHOP_FONT_URL}/${ctaSetting?.price_link_font}.css`} rel="stylesheet" /> : null}
        {ctaSetting?.feature_link_font ? <link href={`${TAGGSHOP_FONT_URL}/${ctaSetting?.feature_link_font}.css`} rel="stylesheet" /> : null}
        { ctaSetting?.discount_link_font ? <link href={`${TAGGSHOP_FONT_URL}/${ctaSetting?.discount_link_font}.css`} rel="stylesheet" />:null}
        {ctaSetting?.feature_link_font ? <link href={`${TAGGSHOP_FONT_URL}/${ctaSetting?.feature_link_font}.css`} rel="stylesheet" /> : null}
        {ctaSetting?.button_link_font ? <link href={`${TAGGSHOP_FONT_URL}/${ctaSetting?.button_link_font}.css`} rel="stylesheet" /> : null}

        <style>{`
        .tb_pro_title_user {font-family: ${ctaSetting?.feature_css_font}; font-size: ${ctaSetting?.feature_font_size}; color: ${ctaSetting?.feature_text_color}}
        .tb_author_avatar0012{background: ${networkData.color}!important}
        .ugc-product-title{ font-family: ${ctaSetting?.title_css_font}!important; color: ${ctaSetting?.product_title_color}!important; font-size:${ctaSetting?.product_title_font_size}px!important}
        .tb_pro_discount_price{ font-family: ${ctaSetting?.discount_css_font}!important; color: ${ctaSetting?.discount_price_color}!important; font-size:${ctaSetting?.discount_price_font_size}px!important}
        .tb_pro_price{ font-family: ${ctaSetting?.price_css_font}!important; color: ${ctaSetting?.original_price_color}!important; font-size:${ctaSetting?.original_price_font_size}px!important}
        .ugc-product-link a{ font-family: ${ctaSetting?.button_css_font}!important; color: ${ctaSetting?.buy_now_color}!important; font-size:${ctaSetting.buy_font_size}px!important; background:${ctaSetting.buy_now_background}!important;}
        `}
         

        </style>

            </Helmet>: "";
  }
}
