import React, { PureComponent } from "react";
import { Scrollbars } from 'react-custom-scrollbars';
import PopUpMedia from "./Elements/PopUpMedia";
import PopUpAuthor from "./Elements/PopUpAuthor";
import PopUpContent from "./Elements/PopUpContent"
import PopUpShare from "./Elements/PopUpShare";
import PopUpStyles from "./popUpStyles";
import PopUpProducts from './Elements/PopUpProducts'
import { isValidAuthorImage, IMG_DATA_FILTER, findNetworkDataFromWebFilters, convertObjectToArray } from '../../../utils';
import ErrorFound from '../../Components/errorFallback'
import PopUpBranding from "./Elements/PopUpBranding";
import PopUpMediaSlider from "./Elements/PopUpMediaSlider";


const TB_WRAPPER = ({ item, personalization, currentIndex, ugc_products, wall }) => {
    return <>
        {item.ugc_personalizaion.UgcSetting && item.ugc_personalizaion.UgcSetting.status == 1 && personalization.themeType == 1 && item.ugc_products &&
            Object.keys(item.ugc_products).length > 0 ? <div className="tb_post_wrap_in"><PopUpProducts wall={wall} item={item} personalization={personalization} currentIndex={currentIndex} ugc_products={ugc_products} /> </div> : null
        }
    </>
}

export default class PostPopUp extends PureComponent {

    state = { currentIndex: this.props.currentIndex, item: this.props.item, isVideoError: false, windowWidth: window.innerWidth, isAuthorImagevalid: true, instaImg: IMG_DATA_FILTER(this.props.imgData, this.props.item.link), isHotspot: false }

    componentDidMount() {
        const { currentIndex, item, slideWithArrowKeys } = this.props;
        const isAuthorImagevalid = isValidAuthorImage(item.author.picture);
        this.setState({ currentIndex: currentIndex, item: item, isAuthorImagevalid: isAuthorImagevalid, instaImg: IMG_DATA_FILTER(this.props.imgData, this.props.item.link) });

        var systemKeyEnable = document.querySelector("#modalPost0");
        if (systemKeyEnable) {  systemKeyEnable.focus(); }

          document.addEventListener("keydown", slideWithArrowKeys);

     }


    componentWillReceiveProps(nextProps) {
        const { currentIndex, item } = nextProps; const isAuthorImagevalid = isValidAuthorImage(item.author.picture);
        this.setState({ currentIndex: currentIndex, item: item, isVideoError: false, isAuthorImagevalid: isAuthorImagevalid, instaImg: IMG_DATA_FILTER(this.props.imgData, this.props.item.link) });
    }
    onErrorVideo = (e) => this.setState({ isVideoError: true })
    onMediaUpdate = (e) => this.setState({ isAuthorImagevalid: false });


    render() {
        const { personalization, onClosePopUp, featuredPopSliderChangeIndex, slideWithArrowKeys, ctaSetting, viewOnText, wall, webFilters } = this.props;
        const { currentIndex, item, isVideoError, windowWidth, instaImg } = this.state;
        const ugc_products = convertObjectToArray(item.ugc_products);
        const networkData = findNetworkDataFromWebFilters(item.network, webFilters) 
        const tb_sc_post_card_bg_color = {backgroundColor:wall.ThemeRule.cardColor}
        

        return <>
            <PopUpStyles wall={wall} item={item} networkData={networkData} ctaSetting={ctaSetting}/>
            <div className={`modalId${item.id} tb_modal_001 tb_fade ${item.type == 4 ||
                    item.type == 5 ||
                    (item.ugc_products && Object.keys(item.ugc_products).length > 0)
                    ? `modal-text-image`
                    : item.type == 2 || item.type == 3
                        ? `modal-only-image`
                        : `modal-only-text`
                    }`} onKeyDown={slideWithArrowKeys} onKeyPress={slideWithArrowKeys} onLoad={slideWithArrowKeys} onClick={(e) => e.currentTarget == e.target && onClosePopUp(e)} >
                <div className="tb_modal_dialog taggModal">
                    <div className="tb_conetent">
                        <div className="tb_modal_head">
                            <div className="tb_modal_header">
                            <div className="tb_post_link_small"><div className="tb_post_address"><a className="Instagram Business" rel="noreferrer" target="_blank" href={item.link}><span>{viewOnText} {networkData.name}</span><svg id="Group_4485" data-name="Group 4485" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><rect id="Rectangle_2094" data-name="Rectangle 2094" width="48" height="48" fill="none" opacity="0"></rect><g id="Group" transform="translate(7.954 4.094)"><path id="Path" d="M33.729,21.333a1.094,1.094,0,0,1,1.559,0l6.837,6.837a1.094,1.094,0,0,1,0,1.559l-6.837,6.837a1.1,1.1,0,0,1-1.559-1.559l6.044-6.072-6.044-6.072a1.094,1.094,0,0,1,0-1.53Z" transform="translate(-33.407 -21.007)" fill="#545454"></path></g></svg></a></div></div> 
                                <div className="tb_close_wrap">
                                    <div className="tb_close_btn" data-dismiss="modal" onClick={onClosePopUp} >
                                        <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 20 20" fill="none">
                                            <path d="M19.457 18.8906C19.1055 19.2422 18.4609 19.2422 18.1094 18.8906L10.375 11.0977L2.58203 18.8906C2.23047 19.2422 1.58594 19.2422 1.23438 18.8906C0.882812 18.5391 0.882812 17.8945 1.23438 17.543L9.02734 9.75L1.23438 2.01562C0.882812 1.66406 0.882812 1.01953 1.23438 0.667969C1.58594 0.316406 2.23047 0.316406 2.58203 0.667969L10.375 8.46094L18.1094 0.667969C18.4609 0.316406 19.1055 0.316406 19.457 0.667969C19.8086 1.01953 19.8086 1.66406 19.457 2.01562L11.6641 9.75L19.457 17.543C19.8086 17.8945 19.8086 18.5391 19.457 18.8906Z" fill="white"/>
                                        </svg>
                                    </div>
                                </div>
                            </div>
                            {personalization.popupSlideShow == 1 ? (
                                <div className="tb_navigation tb_nav_content">
                                     <div className="tb_nav tb_prev_btn" onClick={featuredPopSliderChangeIndex(currentIndex - 1)}>
                                        <svg xmlns="http://www.w3.org/2000/svg" width="15" height="27" viewBox="0 0 15 27" fill="none">
                                            <path d="M13.7109 0.917964C13.9453 1.09375 14.0039 1.32812 14.0039 1.5625C14.0039 1.79687 13.9453 2.03125 13.7695 2.20703L3.10547 13.75L13.7109 25.3516C14.0625 25.7031 14.0625 26.2891 13.6523 26.6406C13.3008 26.9922 12.7148 26.9922 12.3633 26.582L1.11328 14.3945C0.761719 14.043 0.761719 13.5156 1.11328 13.1641L12.3633 0.976558C12.7734 0.566402 13.3594 0.566402 13.7109 0.917964Z" fill="white"/>
                                        </svg>
                                    </div>
                                    <div className="tb_nav tb_nxt_btn" id="wq12" onClick={featuredPopSliderChangeIndex(currentIndex + 1)}>
                                        <svg xmlns="http://www.w3.org/2000/svg" width="14" height="27" viewBox="0 0 14 27" fill="none">
                                            <path d="M0.980469 26.6406C0.746094 26.4648 0.628906 26.2305 0.628906 25.9961C0.628906 25.7617 0.746094 25.5273 0.921875 25.3516L11.5273 13.8086L0.921875 2.20703C0.570312 1.85546 0.570312 1.26953 0.980469 0.917964C1.33203 0.566402 1.91797 0.566402 2.26953 0.976558L13.5195 13.1641C13.8711 13.5156 13.8711 14.043 13.5195 14.3945L2.26953 26.582C1.91797 26.9922 1.33203 26.9922 0.980469 26.6406Z" fill="white"/>
                                        </svg>
                                    </div>
                                </div>
                            ) : null}
                        </div>
                        <div className="tb_modal_body">
                            <div className="tb_modal_body_wrap">
                                <div className={`tb_details_left tb_modal_network-${item.networkId}`} >
                                    {personalization.popupSlideShow == 1 ? (
                                        <div className="tb_navigation tb_nav_img">
                                            <div className="tb_nav tb_prev_btn" onClick={featuredPopSliderChangeIndex(currentIndex - 1)}   >
                                                <svg xmlns="http://www.w3.org/2000/svg" width="15" height="27" viewBox="0 0 15 27" fill="none">
                                                    <path d="M13.7109 0.917964C13.9453 1.09375 14.0039 1.32812 14.0039 1.5625C14.0039 1.79687 13.9453 2.03125 13.7695 2.20703L3.10547 13.75L13.7109 25.3516C14.0625 25.7031 14.0625 26.2891 13.6523 26.6406C13.3008 26.9922 12.7148 26.9922 12.3633 26.582L1.11328 14.3945C0.761719 14.043 0.761719 13.5156 1.11328 13.1641L12.3633 0.976558C12.7734 0.566402 13.3594 0.566402 13.7109 0.917964Z" fill="white"/>
                                                </svg>
                                            </div>
                                            <div className="tb_nav tb_nxt_btn " onClick={featuredPopSliderChangeIndex(currentIndex + 1)}  >
                                                <svg xmlns="http://www.w3.org/2000/svg" width="14" height="27" viewBox="0 0 14 27" fill="none">
                                                    <path d="M0.980469 26.6406C0.746094 26.4648 0.628906 26.2305 0.628906 25.9961C0.628906 25.7617 0.746094 25.5273 0.921875 25.3516L11.5273 13.8086L0.921875 2.20703C0.570312 1.85546 0.570312 1.26953 0.980469 0.917964C1.33203 0.566402 1.91797 0.566402 2.26953 0.976558L13.5195 13.1641C13.8711 13.5156 13.8711 14.043 13.5195 14.3945L2.26953 26.582C1.91797 26.9922 1.33203 26.9922 0.980469 26.6406Z" fill="white"/>
                                                </svg>
                                            </div>
                                        </div>
                                    ) : null}
                                    {item.file ? item.imageList && item.imageList.length > 0 ? <PopUpMediaSlider wall={wall} item={item} instaImg={instaImg} ugc_products={ugc_products} isVideoError={isVideoError} /> : <PopUpMedia wall={wall} item={item} instaImg={instaImg} ugc_products={ugc_products} isVideoError={isVideoError} custmClass={"tb_media_wrap_002"} ImageUrl={item.filename} videoUrl={item.mediaUrl} active={true}/>:''}
                                </div>
                                <div className="tb_details_right" style={tb_sc_post_card_bg_color}>
                                    <div className="tb_content_wrap">
                                        <div className="tb_post_detail_wrap">
                                            {windowWidth < 991 ?    
                                                <div className="tb_post_wrapper">
                                                    <div className="tb_modal_content_001">
                                                        <TB_WRAPPER item={item} wall={wall} personalization={personalization} currentIndex={currentIndex} ugc_products={ugc_products} />
                                                        <PopUpAuthor item={item} personalization={personalization} isAuthorImagevalid={this.state.isAuthorImagevalid} onMediaUpdate={this.onMediaUpdate} wall={wall} networkData={networkData} />
                                                        <ErrorFound><PopUpContent item={item} personalization={personalization} currentIndex={currentIndex} wall={wall} /></ErrorFound>
                                                    </div>
                                                </div>
                                                : <Scrollbars autoHide autoHeightMax={200} className="tb_post_wrapper" style={{ height: 'calc(100% - 35px)' }}
                                                    renderTrackHorizontal={props => <div {...props} className="track-horizontal" style={{ display: "none" }} />}
                                                >
                                                    <div className="tb_modal_content_001">
                                                        <TB_WRAPPER item={item} wall={wall} personalization={personalization} currentIndex={currentIndex} ugc_products={ugc_products} />
                                                        <PopUpAuthor item={item} personalization={personalization} isAuthorImagevalid={this.state.isAuthorImagevalid} onMediaUpdate={this.onMediaUpdate} wall={wall} networkData={networkData} />
                                                        <ErrorFound><PopUpContent item={item} personalization={personalization} currentIndex={currentIndex} wall={wall} /></ErrorFound>
                                                    </div>
                                                </Scrollbars>
                                            }

                                            {/* {(wall.ThemeRule.shareOption === 1 || wall.Personalization.commerceTheme == 4)  &&  ![16,50,53,55].includes(wall.Personalization.commerceTheme) ? <PopUpShare  wall={wall} item={item}/> : null} */}
                                            {wall.ThemeRule.socialAction ? <PopUpShare  wall={wall} item={item}/>:''}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        {wall && wall.UserRule && wall.UserRule.taggBranding==`1` ? <PopUpBranding />:''}
                    </div>
                </div>
            </div>
        </>
    }
}