import React, { Component } from 'react';
import { RATINGS_PATH } from '../../../../constants'
import { STRING_TO_URL_CONVERT } from '../../../../utils'
import { v4 as uuidv4 } from 'uuid';
import ReactHtmlParser from 'react-html-parser';
const reactStringReplace = require("react-string-replace");

const fontColorIfWhite = (white, color, defaultColor, cardColor) => {
    if (cardColor === white) return white == color ? defaultColor : color;
    else if (cardColor === defaultColor) return defaultColor == color ? white : color;
    return white == color ? defaultColor : color;
}

class PopUpContent extends Component {
    state = {
        isTrimEnable: false
    }

    onClickReadText = event => {
        const { isTrimEnable } = this.state;
        this.setState({ isTrimEnable: !isTrimEnable });
    }

    render() {
        const { item, wall } = this.props;
        const {isTrimEnable}=this.state;
        const ratingImg ={backgroundImage: `url(${RATINGS_PATH}${item.networkId}/${item.rating}.png)`}
        const fontCss = {fontFamily: wall?.ThemeRule?.css_font, fontSize: item.type==1 ? wall?.Personalization?.textDecorate==1 ? wall?.ThemeRule?.fontSizeDecorate : wall?.ThemeRule?.fontSizeNormal : wall?.ThemeRule?.fontSize, color: fontColorIfWhite("#ffffff",wall?.ThemeRule?.fontColor,"#000000",wall?.ThemeRule?.cardColor)}

         let content1 = STRING_TO_URL_CONVERT(item.content);
 
         let content2 = content1
 
         let trimContent = String(content2).substring(0, 200)  ;
         let content = isTrimEnable ?  content2: trimContent ;

         console.log("helooo",wall?.ThemeRule?.popupContent)

        return wall && wall.ThemeRule && Object.keys(wall.ThemeRule).length > 0 && (
            <div className="tb_post_content">

                {item.rating != null && item.rating != 0 && ( <div className="tb_post_rating_wrap"> <div className="tb_post_rating" style={ratingImg} ><div></div></div> </div> )}

                <div className="tb_post_rating_wrap"> </div>
               {wall?.ThemeRule?.popupContent ? 
                <div className={`tb_post_content001 ${wall?.Personalization?.textDecorate === 1 && item.type === 1 ? item.textDecoClass : ""}   tb-cTSfont-${wall?.ThemeRule?.font_varient}`} style={fontCss}>
                    {/* <ContentConversion
                        trimContent={Math.trunc(1000 / 5)}
                        fdata={item}
                        key={currentIndex}
                        contentData={item.content}
                        fullcontentData={item.content}
                        personalization={personalization}
                        key={uuidv4()} 
                    /> */}

               {
                wall?.Personalization?.hashtag_highlight !== 0 ?
                    wall?.Personalization?.hashtag_all === 1 ? reactStringReplace(ReactHtmlParser(content), /#(\w+)/g, (match, i) => (
                        <div key={uuidv4()} className="ts_popup_hash_tag" style={{ color: wall?.Personalization?.hashtag_color, fontWeight: "bold" }}>#{match}</div>
                    ))
                        : wall?.Personalization?.hashtag_feed === 1 ? reactStringReplace(ReactHtmlParser(content), item.hash.hashString, (match, i) => (
                            <div key={uuidv4()} className="ts_popup_hash_tag" style={{ color: wall?.Personalization?.hashtag_color, fontWeight: "bold" }}>{match}</div>
                        )) : ReactHtmlParser(content)
                    : ReactHtmlParser(content)
                 }
                 {content.length > 199 ?  !this.state.isTrimEnable ?  <a className="morelink" onClick={this.onClickReadText}> Read More...</a> : <a className="lesslink" onClick={this.onClickReadText}> Read Less</a>:''}
                 

                


                </div>:''}
            </div>
        );
    }
}

export default PopUpContent;