import React, { Component } from 'react';
import Iframe from 'react-iframe';
import ReactPlayer from "react-player"
import { connect } from 'react-redux';
import { showHotspot } from "../../../../../../../../../../actions/themeActions/themeActions";
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO } from '../../../../utils'
import Image from './Media/Image'

 import Tiktoks from './tiktok'
import Hotspot from '../../../Themes/Elements/HotSpot';
import { SET_MEDIA_SIZE } from '../../../../../../../../../../reducer/themes/reducersKeys';

class PopUpMedia extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isHotspot: false,
            loadedImg:false,
            mediaLoaded: false,
            imgUrl: props.ImageUrl
        }
        this.myRef = React.createRef();
    }
    componentWillReceiveProps(nextProps) {
    const { ImageUrl } = nextProps;
    if(this.state.imgUrl !== ImageUrl){
      this.setState({ imgUrl: ImageUrl,mediaLoaded:false })
    }
  }
    
    updateMediaSize = (e) => {
        const { setMediaSize } = this.props;
        const mediaHeight = e.target.offsetHeight
        const mediaWidth = e.target.offsetWidth
        setMediaSize({mediaHeight, mediaWidth})
        this.setState({mediaLoaded : true})
        console.log('mediaWidth,mediaHeight', mediaWidth,mediaHeight)
    }

    render() {
        const { item, instaImg, ugc_products, isVideoError, wall, custmClass, ImageUrl, videoUrl, active, showHotspot,mediaWidth, mediaHeight} = this.props;
        const {loadedImg, mediaLoaded}=this.state;

        return (
            <div className="tb_media_holder" key={`postID${item.id}`}>
                { active ? 
                // <div className={custmClass}>
                <>
                    {(item.type === 2 || item.type === 4) ? <div className="tb_img_holder" ref={this.myRef} onMouseEnter={() =>showHotspot({showAll: true})} onMouseLeave={() =>showHotspot({showAll: false})} style={{minHeight:!mediaLoaded?mediaHeight:null, minWidth:!mediaLoaded?mediaWidth:null}}>
                    <img src={ImageUrl} onLoad={(e) => this.setState({ loadedImg: true }, () => this.updateMediaSize(e))} alt='popupImg' />
                    {(item.type == 2 || item.type == 4) && (item.hotspot == 1 && item.ugc_personalizaion.UgcSetting.status == 1) ? <Hotspot item={item} ugc_products={ugc_products} UgcSetting={item.ugc_personalizaion.UgcSetting} /> : null}
                     {!loadedImg ? <div className="sk_loader_product"><div></div></div>
                     : <>
                      {/* <div className="tb_img_blur__0" style={{ backgroundImage: `url(${ImageUrl})` }}><div></div></div> */}
                      {/* <Image  ImageClass={'tb_modal_npopup_img'}  ImageUrl={ImageUrl} ugc_products={ugc_products} item={item} wall={wall} /> */}
                      </>}
                    
                       
                      </div>

                        : item.type === 1 ? <></>
                            : (item.type === 3 || item.type === 5) &&
                                (item.link.indexOf("youtube") >= 0 ||
                                    item.link.indexOf("youtu.be") >= 0 ||
                                    item.link.indexOf("vimeo") >= 0 ||
                                    item.link.indexOf("tumblr") >= 0 || (item.link.indexOf("vk") >= 0 && item.networkId == 30)) ? (
                                        <>
                                        <div className="tb_img_holder" style={{minHeight:!mediaLoaded?mediaHeight:null, minWidth:!mediaLoaded?mediaWidth:null}}>
                                                    <img src={ImageUrl} style={{opacity: 0}} onLoad={(e) => this.setState({ loadedImg: true }, () => this.updateMediaSize(e))} alt='popupImg'  />
                                                </div>
                                    <div className="tb_video_holder" style={{minHeight:!mediaLoaded?mediaHeight:null, minWidth:!mediaLoaded?mediaWidth:null}}>
                                        {/* <div className="tb_modal_img_holder_001"> */}
                                                <Iframe
                                                    className="tb-detail-image-iframe"
                                                    frameBorder="0"
                                                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                                    src={`${videoUrl}`}
                                                    allowFullScreen="true"
                                                    style={{ width: "100%!important" }}
                                                    onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e) }}
                                                    data-type="video"
                                                    data-stories={item.stories}
                                                    data-filter-id={item.filterId}
                                                    data-network={item.networkId}
                                                    data-link={item.link}
                                                    data-wall-id={wall.Wall.id} data-item-id={item.id}
                                                    data-load={0}
                                                ></Iframe>
                                        {/* </div> */}
                                    </div>
                                    </>
                                ) : isVideoError ? (
                                    <div className="tb_img_holder" style={{minHeight:!mediaLoaded?mediaHeight:null, minWidth:!mediaLoaded?mediaWidth:null}}> <div className="tb_modal_img_holder_001"><img
                                        src="https://app.taggbox.com/widget/image/no-camera.svg"
                                        style={{
                                            visibility: "visible!important",
                                            width: 180,
                                            margin: "20px auto",
                                            objectFit: "contain",
                                        }}
                                        alt='popupImg' 
                                    /> </div>
                                    </div>
                                ) : (item.link.indexOf("facebook") >= 0) ? <div className="tb_img_holder" style={{minHeight:!mediaLoaded?mediaHeight:null, minWidth:!mediaLoaded?mediaWidth:null}}>
                                    {/* <div className="tb_modal_img_holder_001"> */}
                                    <ReactPlayer
                                        style={{ display: 'contents' }}
                                        className="tb-detail-image-iframe"
                                        url={videoUrl}
                                     data-type="video"
                                        data-stories={item.stories}
                                        data-filter-id={item.filterId}
                                        data-network={item.networkId}
                                        data-link={item.link}
                                        data-wall-id={wall.Wall.id} data-item-id={item.id}
                                        data-load={0}
                                        onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e) }}
                                        autoPlay
                                    /></div>
                                    // </div> 
                                    // : item.networkId == 28 ? <div className="tb_img_holder"><div className="tb_modal_img_holder_001">
                                    //     <div className="tb_tiktok_video_wrap">
                                    //          <Tiktoks data={item} key={item.id} />
                                    //     </div></div>
                                    // </div>
                                     :
                                            (
                                                <>
                                                    <div className="tb_img_holder" style={{minHeight:!mediaLoaded?mediaHeight:null, minWidth:!mediaLoaded?mediaWidth:null}}>
                                                    <img src={ImageUrl} style={{opacity: 0}} onLoad={(e) => this.setState({ loadedImg: true }, () => this.updateMediaSize(e))} alt='popupImg'  />
                                                </div>
                                                    <div className="tb_video_holder" style={{minHeight:!mediaLoaded?mediaHeight:null, minWidth:!mediaLoaded?mediaWidth:null}}>
                                                {/* <div className="tb_modal_img_holder_001"> */}
                                                        <video
                                                            src={videoUrl}
                                                        
                                                            autoPlay
                                                            data-type="video"
                                                            data-stories={item.stories}
                                                            data-filter-id={item.filterId}
                                                            data-network={item.networkId}
                                                            data-link={item.link}
                                                            onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e) }}
                                                            data-load={0}
                                                            poster={item.postFileNew}
                                                            data-wall-id={wall.Wall.id} data-item-id={item.id}
                                                        ></video>
                                                    {/* </div> */}
                                                </div>
                                                </>
                                            )}
                    {item.networkId == 24 ? (
                        <div className="tb_img_holder" style={{minHeight:!mediaLoaded?mediaHeight:null, minWidth:!mediaLoaded?mediaWidth:null}}> 
                        {/* <div className="tb_modal_img_holder_001">  */}
                        <img
                            src="img/loading.gif"
                            alt=""
                            className="img-responsive spotify-img"
                            style={{ visibility: "visible", position: "relative", transform: "translate(-50%, -50%)", top: "50%", left: "50%", display: "none", width: "60px", }}
                        /> </div>
                        // </div>
                    ) : null}

                {/* </div>  */}
                </>
                : 
                // <div className={custmClass}>
                           <div className="tb_img_holder" ref={this.myRef}>
                            <div className="tb_img_blur__0" style={{ backgroundImage: `url(${ImageUrl})` }}><div></div></div>
                            <Image  ImageClass={'tb_modal_npopup_img'}  ImageUrl={ImageUrl} ugc_products={ugc_products} item={item} wall={wall}/>
                        </div>
                        // </div>
                        }

            </div>
        );
    }
}

const mapStateToProps = state => ({
    mediaHeight: state.modalPop.mediaHeight ,
    mediaWidth: state.modalPop.mediaWidth
})
const mapDispatchToProps = dispatch => {
  return {
    setMediaSize: (data) => dispatch({type: SET_MEDIA_SIZE, payload: data}),
   showHotspot: (data) => dispatch(showHotspot(data))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(PopUpMedia);